export default {
	data() {
		return {
			list: {},
			isOrder: false
		};
	},
	onLoad: function(option) {
		this.loadData();
		if (option.order) {
			this.isOrder = true;
		}
	},
	onShow() {
		if (uni.getStorageSync('address-load')) {
			this.loadData();
			uni.removeStorageSync('address-load');
		}
	},
	methods: {
		loadData() {
			this.$minApi
				.address({})
				.then(res => {
					this.list = res;
				})
				.catch(err => {
					console.log(err);
				});
		},
		toEdit(val) {
			uni.setStorageSync('address-edit', val);
			uni.navigateTo({
				url: '/pages/user/address/edit'
			});
		},
		del(id) {
			this.$minApi
				.addressDel({ id: id })
				.then(res => {
					this.loadData();
				})
				.catch(err => {
					console.log(err);
				});
		},
		clickItem(item) {
			if (this.isOrder) {
				uni.setStorageSync('address-select', item);
				uni.navigateBack({
					delta: 1
				});
			}
		}
	}
};
